import React from "react";
import Lightbox from "lightbox-react";
import "lightbox-react/style.css"; // This only needs to be imported once in your app
import MainVisual from "./../images/fullsize.jpg";


export default class LBImage extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isOpen: false
    };
  }

  render() {
    const { isOpen } = this.state;

    return (
      <div>
        <button
          className="text-small"
          type="button"
          onClick={() => this.setState({ isOpen: true })}
        >
          
        </button>

        {isOpen && (
          <Lightbox
            mainSrc={MainVisual}
            onCloseRequest={() => this.setState({ isOpen: false })}
            imageCaption={
              <div className="image-caption">
                <a href="https://twitter.com/saetsukura" className="image-caption-name">
                  <h1 className="image-caption-title">
                    <span className="image-caption-drawing"></span>
                    <span className="image-caption-artist"></span>
                    <span className="image-caption-id">(@saetsukura)</span>
                  </h1>
                </a>
                <p class="image-caption-contents"><span class="image-caption-contents-line-1"></span><span class="image-caption-contents-line-2">10</span></p>
              </div>
            }
          />
        )}
      </div>
    );
  }
}
